.sticky-summary {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  z-index: 3;
  top: 50px;
  left: 0;
  width: 100%;
  padding-top: 40px;
  padding-bottom: 5px;
  border-bottom: 3px solid #9ec738;
  background-color: white;
  transition: visibility 0.2s ease 0.2s, opacity 0.2s ease 0s;
}

@media (max-width: 991px) {
  .sticky-summary {
    display: none;
  }
}

.sticky-summary.active {
  visibility: visible;
  opacity: 1.0;
  transition: visibility 0.2s ease 0s, opacity 0.2s ease 0s;
}

.sticky-summary .product-img, .sticky-summary .product-description, .sticky-summary .variations {
  display: inline-block;
  vertical-align: middle;
}

.sticky-summary .product-img {
