/* Form Base Styles
***********************************************/
input {
  outline: none;
}

.form-inline p {
  display: inline-block;
}

.form-control {
  height: 41px;
  font-family: "Source Sans Pro", sans-serif;
  box-shadow: none;
  background-color: #f7f7f7;
  border-radius: 4px;
  border: 1px solid #d9d9d9;
  box-shadow: inset 2px 2px 10px 0px rgba(0, 0, 0, 0.1);
}

.form-control:focus {
  box-shadow: none;
}

.form-control.form-control-sm {
  height: 43px;
}

.form-control.form-control-lg {
  height: 51px;
}

label {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 400;
}

/* Custom Checkboxes
***********************************************/
/* Search Filters */
.filter-list .checkbox input[type="checkbox"] {
  display: none;
}

.filter-list .checkbox label {
  padding: 0;
  margin: 0;
}

.filter-list .checkbox label span {
  position: relative;
  padding-left: 30px;
  color: #642c90;
}

.filter-list .checkbox label span:before {
  content: '';
  position: absolute;
  top: -2px;
  left: 0;
  height: 20px;
  width: 20px;
  text-align: center;
  line-heiht: 20px;
  border: 1px solid #642c90;
}

.filter-list .checkbox label input[type="checkbox"]:checked + span:before {
  content: '\f00c';
  font-size: 0.8em;
  font-family: FontAwesome;
  color: white;
  background-color: #9ec738;
  border: 1px solid #88ab30;
}

/* Custom Radio
***********************************************/
/* Product Variations - Radio */
.variation-radio {
  display: inline-block;
  margin-right: 10px;
}

.variation-radio input[type="radio"] {
  display: none;
}

.variation-radio label {
  padding: 0;
  margin: 0;
  cursor: pointer;
}

.variation-radio label span {
  position: relative;
  display: block;
  top: 0;
  height: 41px;
  margin: 0;
  padding: 0 15px;
  font-family: "Source Sans Pro", sans-serif;
  line-height: 41px;
  background-color: #f7f7f7;
  border-radius: 4px;
  border: 1px solid #d9d9d9;
}

.variation-radio label input[type="radio"]:checked + span {
  color: white;
  background-color: #b239a3;
  background: #b239a3;
  background: -moz-linear-gradient(top, #b239a3 0%, #9f3391 100%);
  background: -webkit-linear-gradient(top, #b239a3 0%, #9f3391 100%);
  background: linear-gradient(to bottom, #b239a3 0%, #9f3391 100%);
  border-color: #8b2d80;
}

/* Large Radio */
.custom-radio input[type="radio"] {
  display: none;
}

.custom-radio label {
  position: relative;
  padding-left: 45px;
  margin-bottom: 15px;
}

.custom-radio label span {
  font-size: 1.2em;
  color: #b239a3;
}

.custom-radio label span small {
  display: block;
  color: #555555;
}

.custom-radio label span:before {
  content: '';
  position: absolute;
  left: 0;
  top: 5px;
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid #9e9e9e;
  border-radius: 50%;
}

.custom-radio label input[type="radio"]:checked + span:before {
  background: #b239a3;
  border-color: #642c90;
}

.custom-radio label input[type="radio"]:checked + span:after {
  content: '';
  position: absolute;
  top: 15px;
  left: 10px;
  display: block;
  width: 10px;
  height: 10px;
  background: white;
  border: 1px solid #642c90;
  border-radius: 50%;
}

.inline-radio-list .custom-radio {
  display: inline-block;
  vertical-align: top;
  width: 20%;
  margin-top: 0;
  margin-right: -4px;
  text-align: center;
}

.inline-radio-list .custom-radio label {
  width: 100%;
  padding-left: 0;
  padding-top: 45px;
}

.inline-radio-list .custom-radio label span:before {
  left: calc(50% - 15px);
}

.inline-radio-list .custom-radio label input[type="radio"]:checked + span:after {
  left: calc(50% - 5px);
}

/* Toggle Radio
***********************************************/
.switch {
  position: relative;
  display: inline-block;
  width: 48px;
  height: 26px;
}

.switch input {
  display: none;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #3b1a55;
  border: 1px solid #7f38b7;
  border-radius: 26px;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 2px;
  bottom: 2px;
  background: white;
  background: -moz-linear-gradient(top, white 50%, #f7f7f7 51%, #f7f7f7 51%);
  background: -webkit-linear-gradient(top, white 50%, #f7f7f7 51%, #f7f7f7 51%);
  background: linear-gradient(to bottom, white 50%, #f7f7f7 51%, #f7f7f7 51%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#f7f7f7',GradientType=0 );
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(22px);
  -ms-transform: translateX(22px);
  transform: translateX(22px);
}

/* Custom Select
***********************************************/
.custom-select {
  height: 41px;
}

.custom-select.full-width {
  display: block;
}

@supports (pointer-events: none) and ((-webkit-appearance: none) or (-moz-appearance: none) or (appearance: none)) {
  .custom-select {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    font-weight: 400;
  }
  .custom-select:before, .custom-select:after {
    content: "";
    position: absolute;
    pointer-events: none;
  }
  .custom-select:after {
    content: "\25BC";
    height: 1em;
    right: 18px;
    top: 50%;
    margin-top: -.5em;
    color: white;
    font-size: .525em;
    line-height: 1;
    transition: top 0.2s ease 0s;
  }
  .custom-select:before {
    top: 0;
    width: 41px;
    height: 100%;
    right: 0px;
    top: 0;
    bottom: 0;
    background-color: #b239a3;
    background: #b239a3;
    background: -moz-linear-gradient(top, #b239a3 0%, #9f3391 100%);
    background: -webkit-linear-gradient(top, #b239a3 0%, #9f3391 100%);
    background: linear-gradient(to bottom, #b239a3 0%, #9f3391 100%);
    border: 1px solid #9f3391;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    box-shadow: 0px 0px 0px 0px #c955ba;
    transition: box-shadow 0.2s ease 0s;
  }
  .custom-select:hover:after, .custom-select:focus:after {
    top: 50%;
    transition: top 0.2s ease 0s;
  }
  .custom-select:hover:before, .custom-select:focus:before {
    top: 0;
    box-shadow: 0px 0px 10px 0px #c955ba;
    transition: box-shadow 0.2s ease 0s;
  }
  .custom-select .custom-dropdown {
    height: 100%;
    padding-right: 53px;
    font-size: 1em;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    background-color: #f7f7f7;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
  }
  @media (max-width: 1259px) {
    .custom-select .custom-dropdown {
      font-size: 0.8em;
    }
  }
  /* FF only temp fix */
  @-moz-document url-prefix() {
    .custom-dropdown {
      padding-right: .9em;
    }
    /*.custom-dropdown--large .custom-dropdown { padding-right: 1.3em }*/
    /*.custom-dropdown--small .custom-dropdown { padding-right: .5em }*/
  }
}
